/* Importation de Google Fonts pour une typographie moderne */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

/* Styles de base */
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
  color: #333;
}

/* Styles de la navbar */
nav {
  margin-top: 8%;
  z-index: 1000;
  background-color: #6d4c41; /* Couleur de fond inspirée du logo */
  padding: 10px 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: #fff; /* Couleur du texte inspirée du logo */
  text-decoration: none;
  font-size: 18px;
  display: flex;
  align-items: center;
}

nav ul li a:hover {
  text-decoration: underline;
}

/* Styles des icônes */
nav ul li a .fa-home,
nav ul li a .fa-user,
nav ul li a .fa-plus {
  margin-right: 5px;
}

/* Styles des pages */
.page {
  padding: 20px;
}

/* Styles des titres */
h1, h2, h3 {
  color: #6d4c41; /* Couleur des titres inspirée du logo */
}

/* Styles des boutons */
button {
  background-color: #6d4c41; /* Couleur de fond inspirée du logo */
  color: #fff; /* Couleur du texte inspirée du logo */
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: #5d4037; /* Couleur de survol inspirée du logo */
}

/* Styles des formulaires */
form {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

form input, form select, form textarea {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

form button {
  width: 100%;
  background-color: #6d4c41; /* Couleur de fond inspirée du logo */
  color: #fff; /* Couleur du texte inspirée du logo */
  border: none;
  padding: 15px;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
}

form button:hover {
  background-color: #5d4037; /* Couleur de survol inspirée du logo */
}

/* Styles des cartes de logement */
.logement-card {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin: 20px 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.logement-card h2 {
  color: #6d4c41; /* Couleur des titres inspirée du logo */
}

.logement-card p {
  color: #555;
}

/* Styles des images */
.logement-card img {
  width: 100%;
  border-radius: 5px;
}

/* Styles des footers */
footer {
  background-color: #6d4c41; /* Couleur de fond inspirée du logo */
  color: #fff; /* Couleur du texte inspirée du logo */
  text-align: center;
  padding: 10px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
}
